import { useDispatch, useSelector } from 'react-redux';
import { 
  addToCart, 
  removeFromCart, 
  increaseQuantity, 
  decreaseQuantity, 
  updateQuantity, 
  clearCart, 
  toggleCart, 
  openCart, 
  closeCart 
} from '@/lib/redux/slices/cartSlice';
import { saveCartToStorage } from '@/lib/redux/cartPersistence';

export const useCart = () => {
  const dispatch = useDispatch();
  const cart = useSelector((state) => state.cart);

  const addItem = (item) => {
    dispatch(addToCart(item));
    // Save to localStorage after adding
    setTimeout(() => {
      const updatedCart = {
        items: [...cart.items, { ...item, quantity: 1, totalPrice: item.price }],
        totalQuantity: cart.totalQuantity + 1,
        totalAmount: cart.totalAmount + item.price,
      };
      saveCartToStorage(updatedCart);
    }, 0);
  };

  const removeItem = (id) => {
    dispatch(removeFromCart(id));
    // Save to localStorage after removing
    setTimeout(() => {
      const updatedCart = {
        items: cart.items.filter(item => item.id !== id),
        totalQuantity: cart.totalQuantity - (cart.items.find(item => item.id === id)?.quantity || 0),
        totalAmount: cart.items.reduce((total, item) => item.id !== id ? total + item.totalPrice : total, 0),
      };
      saveCartToStorage(updatedCart);
    }, 0);
  };

  const increaseItemQuantity = (id) => {
    dispatch(increaseQuantity(id));
    // Save to localStorage after updating
    setTimeout(() => {
      const updatedCart = {
        items: cart.items.map(item => 
          item.id === id 
            ? { ...item, quantity: item.quantity + 1, totalPrice: (item.quantity + 1) * item.price }
            : item
        ),
        totalQuantity: cart.totalQuantity + 1,
        totalAmount: cart.totalAmount + (cart.items.find(item => item.id === id)?.price || 0),
      };
      saveCartToStorage(updatedCart);
    }, 0);
  };

  const decreaseItemQuantity = (id) => {
    dispatch(decreaseQuantity(id));
    // Save to localStorage after updating
    setTimeout(() => {
      const item = cart.items.find(item => item.id === id);
      const updatedCart = {
        items: item?.quantity === 1 
          ? cart.items.filter(item => item.id !== id)
          : cart.items.map(item => 
              item.id === id 
                ? { ...item, quantity: item.quantity - 1, totalPrice: (item.quantity - 1) * item.price }
                : item
            ),
        totalQuantity: cart.totalQuantity - 1,
        totalAmount: cart.totalAmount - (item?.price || 0),
      };
      saveCartToStorage(updatedCart);
    }, 0);
  };

  const updateItemQuantity = (id, quantity) => {
    dispatch(updateQuantity({ id, quantity }));
    // Save to localStorage after updating
    setTimeout(() => {
      const item = cart.items.find(item => item.id === id);
      if (item) {
        const updatedCart = {
          items: quantity > 0 
            ? cart.items.map(item => 
                item.id === id 
                  ? { ...item, quantity, totalPrice: quantity * item.price }
                  : item
              )
            : cart.items.filter(item => item.id !== id),
          totalQuantity: cart.totalQuantity + (quantity - item.quantity),
          totalAmount: cart.items.reduce((total, item) => 
            item.id === id 
              ? total + (quantity * item.price)
              : total + item.totalPrice, 0
          ),
        };
        saveCartToStorage(updatedCart);
      }
    }, 0);
  };

  const clearAllItems = () => {
    dispatch(clearCart());
    // Clear from localStorage
    setTimeout(() => {
      saveCartToStorage({ items: [], totalQuantity: 0, totalAmount: 0 });
    }, 0);
  };

  const toggleCartOpen = () => {
    dispatch(toggleCart());
  };

  const openCartDrawer = () => {
    dispatch(openCart());
  };

  const closeCartDrawer = () => {
    dispatch(closeCart());
  };

  const isInCart = (id) => {
    return cart.items.some(item => item.id === id);
  };

  const getItemQuantity = (id) => {
    const item = cart.items.find(item => item.id === id);
    return item ? item.quantity : 0;
  };

  return {
    // State
    items: cart.items,
    totalQuantity: cart.totalQuantity,
    totalAmount: cart.totalAmount,
    isOpen: cart.isOpen,
    
    // Actions
    addItem,
    removeItem,
    increaseItemQuantity,
    decreaseItemQuantity,
    updateItemQuantity,
    clearAllItems,
    toggleCartOpen,
    openCartDrawer,
    closeCartDrawer,
    
    // Utilities
    isInCart,
    getItemQuantity,
  };
}; 