"use client";
import { memo } from "react";
import Image from "next/image";
import { Button } from "../ui/button";
import { useCart } from "@/hooks/useCart";

const CategoryCard = memo(({ image, name, weight, store, discount, mrp, price, time, id }) => {
  const { addItem, isInCart } = useCart();

  const handleAddToCart = () => {
    if (!isInCart(id)) {
      addItem({
        id,
        name,
        brand: store,
        price,
        originalPrice: mrp,
        image,
        weight
      });
    }
  };

  // Ensure we have valid values
  const displayName = name || "Unnamed Product";
  const displayWeight = weight || "1 unit";
  const displayStore = store || "Unknown Brand";
  const displayMrp = mrp || 0;
  const displayPrice = price || 0;
  const displayDiscount = discount || 0;
  const displayImage = image || "/categories/default.png";

  // Format the end time for display
  const formatEndTime = (endTime) => {
    if (!endTime) return "No end time";
    
    try {
      const endDate = new Date(endTime);
      const now = new Date();
      
      // Check if deal has expired
      if (endDate < now) {
        return "Deal Expired";
      }
      
      // Format the date
      const options = { 
        month: 'short', 
        day: 'numeric', 
        hour: '2-digit', 
        minute: '2-digit',
        hour12: true 
      };
      return `Ends: ${endDate.toLocaleDateString('en-US', options)}`;
    } catch (error) {
      return "Invalid date";
    }
  };

  const endTimeDisplay = formatEndTime(time);

  return (
    <div className="flex flex-col items-start gap-3 w-[230px] h-[390px] border rounded-2xl shadow-md p-4 bg-white hover:shadow-2xl hover:scale-[1.03] transition-all duration-200 ease-in-out cursor-pointer">
      {/* Product Image */}
      <div className="relative flex items-center justify-center w-[194.287px] h-[192.804px] bg-gradient-to-br from-blue-100 via-white to-blue-50 rounded-xl p-6 mb-2">
        <Image
          src={displayImage}
          alt={displayName}
          width={148}
          height={90}
          className="w-[148px] h-[90px] object-contain"
          onError={(e) => {
            // Prevent infinite retries by setting a flag
            if (!e.target.dataset.fallbackSet) {
              e.target.dataset.fallbackSet = 'true';
              e.target.src = "/categories/default.png";
            }
          }}
          onLoad={(e) => {
            // Clear the fallback flag on successful load
            delete e.target.dataset.fallbackSet;
          }}
        />

        <Button
          onClick={handleAddToCart}
          className={`absolute bottom-2 right-10 transform translate-y-1/2 translate-x-1/2 w-[53px] h-[33px] border font-medium rounded-md hover:bg-blue-100 transition shadow-md
            ${isInCart(id) ? "bg-green-50 text-green-500 border-green-400" : "bg-white text-blue-400 border-blue-400"}`}
        >
          {isInCart(id) ? "" : "ADD"}
        </Button>

        {displayDiscount > 0 && (
          <span className="absolute top-2 left-2 bg-gradient-to-r from-red-500 to-pink-500 text-white text-xs font-bold px-3 py-1 rounded-full shadow-md border-2 border-white animate-pulse">
            {displayDiscount}% OFF
          </span>
        )}
      </div>

      <h3 className="text-lg font-bold truncate w-full" title={displayName}>{displayName}</h3>
      <p className="text-black text-md">{displayWeight}</p>
      <p className="text-black text-md">By {displayStore}</p>

      {/* End Time Section */}
      <div className="mt-2 w-full">
        <div className={`text-center text-xs font-semibold p-2 rounded-lg shadow-sm border ${
          endTimeDisplay === "Deal Expired" 
            ? "text-red-600 bg-red-50 border-red-200" 
            : "text-blue-700 bg-blue-50 border-blue-200"
        }`}>
          {endTimeDisplay}
        </div>
      </div>

      <div className="flex items-center gap-2 mt-auto">
        {displayMrp > displayPrice && (
          <p className="text-gray-400 line-through text-md">{displayMrp}</p>
        )}
        <p className="text-lg font-bold">{displayPrice}</p>
      </div>
    </div>
  );
});

CategoryCard.displayName = 'CategoryCard';

export default CategoryCard;