"use client";

import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/app/components/ui/carousel";
import { Skeleton } from "@/app/components/ui/skeleton";
import ProductCard from "@/app/components/ui/ProductCard";

const CategoryCarousel = ({ data = [], loading = false, title = "Category Products" }) => {
  if (loading) {
    return (
      <div className="py-6">
        <h2 className="text-2xl font-bold mb-4">{title}</h2>
        <div className="flex gap-4 px-4">
          {Array.from({ length: 5 }).map((_, i) => (
            <Skeleton key={i} className="w-[230px] h-[390px] rounded-2xl" />
          ))}
        </div>
      </div>
    );
  }

  const mappedProducts = data.map((p) => {
    const mrp = p.mrp || 0;
    const sp = p.sellingPrice || 0;

    return {
      id: p._id || p.id,
      name: p.productName || p.name || "Unnamed",
      weight: p.weight || "1 unit",
      seller: p.brand || p.seller || "Unknown",
      discount: mrp && sp ? Math.round(((mrp - sp) / mrp) * 100) : 0,
      originalPrice: mrp,
      discountedPrice: sp,
      image: p.images?.[0] || p.image || "/home/grocery/broccoli.png",
      time: p.dealEndTime || p.time || null,
    };
  }); 

  return (
    <div className="py-6">
      <h2 className="text-2xl font-bold mb-4">{title}</h2>
      <Carousel className="w-full mx-auto">
        <CarouselContent className="-ml-4 pr-[12%]">
          {mappedProducts.map((product) => (
            <CarouselItem
              key={product.id}
              className="pl-4 md:basis-1/2 lg:basis-1/3 xl:basis-[16.66%] flex-shrink-0"
            >
              <div className="p-1">
                <ProductCard product={product} />
              </div>
            </CarouselItem>
          ))}
        </CarouselContent>
        <CarouselPrevious />
        <CarouselNext />
      </Carousel>
    </div>
  );
};

export default CategoryCarousel; 