"use client";

import { useState, useEffect, useMemo, useCallback } from "react";
import { useParams } from "next/navigation";
import axios from "axios";
import Link from "next/link";
import Image from "next/image";
import Sidebar from "@/components/home/sidebar";
import Header from "@/components/home/Header";
import CategoryCarousel from "@/components/categories/CategoryCarousel";
import { FilterButton } from "../page";
import Footer from "@/components/home/footer";
import { generateDummyProducts, validateImageUrl } from "@/lib/utils";
import CategoryCard from "@/components/categories/CategoryCard";

const CategorySlugPage = () => {
  const { slug } = useParams();
  const [category, setCategory] = useState(null);
  const [subcategories, setSubcategories] = useState([]);
  const [products, setProducts] = useState([]);
  const [selectedSubcategory, setSelectedSubcategory] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isLoadingProducts, setIsLoadingProducts] = useState(false);
  const [error, setError] = useState(null);

  // Memoize the fetch function to prevent unnecessary re-renders
  const fetchCategoryProducts = useCallback(async (categoryId, subcategoryId = null) => {
    try {
      setIsLoadingProducts(true);
      
      let endpoint = `http://localhost:4000/lmd/api/v1/retail/products`;
      let params = {
        page: '1',
        limit: '20',
        sort: '-createdAt',
        status: 'active'
      };
      
      if (subcategoryId) {
        // Fetch products by subcategory
        endpoint = `http://localhost:4000/lmd/api/v1/retail/products/subcategory/${subcategoryId}`;
      } else {
        // Fetch products by category
        params.category = categoryId;
      }

      const response = await axios.get(endpoint, {
        headers: {
          'Content-Type': 'application/json', 
        },
        params: params
      });

      console.log('Products API response:', response.data);

      if (response.data.status === 'success' || response.data.success) {
        const fetchedProducts = response.data.data || response.data.products || [];
        console.log('Fetched products:', fetchedProducts);
        
        if (fetchedProducts.length === 0) {
          // If no products found, use dummy data
          setProducts(generateDummyProducts(10));
        } else {
          setProducts(fetchedProducts);
        }
      } else {
        throw new Error('Failed to fetch products');
      }
    } catch (err) {
      console.error('Error fetching products:', err);
      // Use dummy products as fallback
      setProducts(generateDummyProducts(10));
    } finally {
      setIsLoadingProducts(false);
    }
  }, []);

  // Memoize the subcategory click handler
  const handleSubcategoryClick = useCallback(async (subcategory) => {
    setSelectedSubcategory(subcategory);
    await fetchCategoryProducts(category._id, subcategory._id);
  }, [category?._id, fetchCategoryProducts]);

  // Memoize the all products click handler
  const handleAllProductsClick = useCallback(async () => {
    setSelectedSubcategory(null);
    await fetchCategoryProducts(category._id);
  }, [category?._id, fetchCategoryProducts]);

  useEffect(() => {
    const fetchCategoryData = async () => {
      try {
        setIsLoading(true);
        setError(null);

        // Fetch categories to find the one matching the slug
        const categoriesResponse = await axios.get('http://localhost:4000/lmd/api/v1/retail/categories', {
          headers: {
            'Content-Type': 'application/json',
          }
        });

        if (categoriesResponse.data.success) {
          const categories = categoriesResponse.data.data || [];
          const foundCategory = categories.find(cat => cat.slug === slug);
          
          if (foundCategory) {
            setCategory(foundCategory);
            
            // Fetch subcategories for this category
            try {
              const subcategoriesResponse = await axios.get(`http://localhost:4000/lmd/api/v1/retail/categories/${foundCategory._id}/subcategories`, {
                headers: {
                  'Content-Type': 'application/json',
                }
              });

              if (subcategoriesResponse.data.success) {
                setSubcategories(subcategoriesResponse.data.data || []);
              }
            } catch (subcatError) {
              console.warn('Failed to fetch subcategories:', subcatError);
              setSubcategories([]);
            }

            // Fetch initial products for this category
            await fetchCategoryProducts(foundCategory._id);
          } else {
            setError('Category not found');
            // Use dummy data as fallback
            setCategory({ _id: 'dummy-cat', name: slug, slug: slug });
            setProducts(generateDummyProducts(10));
          }
        } else {
          throw new Error('Failed to fetch categories');
        }
      } catch (err) {
        console.error('Error fetching category data:', err);
        setError('Failed to load category data');
        // Use dummy data as fallback
        setCategory({ _id: 'dummy-cat', name: slug, slug: slug });
        setSubcategories([]);
        setProducts(generateDummyProducts(10));
      } finally {
        setIsLoading(false);
      }
    };

    if (slug) {
      fetchCategoryData();
    }
  }, [slug, fetchCategoryProducts]);

  // Memoize the category title
  const categoryTitle = useMemo(() => {
    return selectedSubcategory ? `${selectedSubcategory.name} Products` : `${category?.name || slug} Products`;
  }, [selectedSubcategory, category?.name, slug]);

  if (isLoading) {
    return (
      <div className="flex-1">
        <Header />
        <div className="p-6 w-full max-w-[1700px] mx-auto">
          <div className="animate-pulse">
            <div className="h-8 bg-gray-200 rounded w-64 mb-4"></div>
            <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
              {[...Array(10)].map((_, i) => (
                <div key={i} className="flex flex-col items-center p-3">
                  <div className="w-20 h-20 md:w-24 md:h-24 bg-gray-200 rounded-lg mb-2"></div>
                  <div className="h-4 bg-gray-200 rounded w-16"></div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }

  if (error && !category) {
    return (
      <div className="flex h-screen items-center justify-center text-red-500 text-2xl">
        {error}
      </div>
    );
  }

  return (
    <div className="flex-1">
      <Header />

      <div className="p-6 w-full max-w-[1700px] mx-auto">
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4 px-2 md:px-6 py-1">
          {/* breadcrumb */}
          <nav className="mb-4 text-2xl">
            <Link href="/categories" className="hover:underline font-medium">
              Categories
            </Link>
            <span className="mx-2">&gt;</span>
            <span className="font-semibold text-yellow-500">
              {category?.name || slug}
            </span>
          </nav>

          <div className="flex gap-2 w-full md:w-auto">
            <FilterButton>Filters</FilterButton>
            <FilterButton>Sort</FilterButton>
          </div>
        </div>

        {/* subcategories */}
        {subcategories.length > 0 && (
          <div className="px-2 md:px-8 mt-6">
            <div className="mb-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-3">Subcategories</h3>
            </div>
            <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
              {/* All Products Option */}
              <SubcategoryItem 
                categorySlug={slug} 
                subcategory={{ _id: 'all', name: 'All Products', slug: 'all' }}
                isSelected={!selectedSubcategory}
                onClick={handleAllProductsClick}
              />
              
              {/* Subcategories */}
              {subcategories.map((subcategory) => (
                <SubcategoryItem 
                  key={subcategory._id} 
                  categorySlug={slug} 
                  subcategory={subcategory}
                  isSelected={selectedSubcategory?._id === subcategory._id}
                  onClick={() => handleSubcategoryClick(subcategory)}
                />
              ))}
            </div>
          </div>
        )}

        {/* Products Section */}
        <div className="mt-8 px-2 md:px-8">
          <div className="mb-4">
            <h3 className="text-lg font-semibold text-gray-900">
              {categoryTitle}
            </h3>
            {isLoadingProducts && (
              <p className="text-sm text-gray-500 mt-1">Loading products...</p>
            )}
          </div>

          {/* Products Grid */}
          {products.length > 0 ? (
            <>
              <div className="text-sm text-gray-500 mb-2">
                Found {products.length} products
              </div>
              <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-6">
                {products.map((product) => {
                  // Handle both API and dummy product formats
                  let mrp, sp;
                  if (product.price && typeof product.price === 'object') {
                    mrp = product.price.base_price || 0;
                    sp = product.price.discounted_price || 0;
                  } else {
                    mrp = product.mrp || product.originalPrice || 0;
                    sp = product.sellingPrice || product.price || 0;
                  }
                  return (
                    <CategoryCard
                      key={product._id || product.id}
                      {...product}
                      mrp={mrp}
                      price={sp}
                    />
                  );
                })}
              </div>
            </>
          ) : !isLoadingProducts ? (
            <div className="text-center py-8 text-gray-500">
              No products found for {selectedSubcategory ? selectedSubcategory.name : category?.name || slug}
            </div>
          ) : null}
        </div>
      </div>
      <Footer />
    </div>
  );
};

const SubcategoryItem = ({ categorySlug, subcategory, isSelected = false, onClick }) => {
  const validatedImage = validateImageUrl(subcategory.imageUrl);
  
  return (
    <div 
      className={`group cursor-pointer ${onClick ? '' : 'pointer-events-none'}`}
      onClick={onClick}
    >
      <div className={`flex flex-col items-center p-3 rounded-xl transition-all ${
        isSelected 
          ? 'bg-blue-50 border-2 border-blue-200' 
          : 'hover:bg-gray-50'
      }`}>
        <div className="relative w-20 h-20 md:w-24 md:h-24">
          <Image
            src={validatedImage}
            alt={subcategory.name}
            fill
            className="object-contain"
            onError={(e) => {
              // Prevent infinite retries by setting a flag
              if (!e.target.dataset.fallbackSet) {
                e.target.dataset.fallbackSet = 'true';
                e.target.src = "/categories/default.png";
              }
            }}
            onLoad={(e) => {
              // Clear the fallback flag on successful load
              delete e.target.dataset.fallbackSet;
            }}
          />
        </div>
        <p className={`text-center mt-2 text-sm md:text-base font-medium ${
          isSelected 
            ? 'text-blue-600' 
            : 'group-hover:text-blue-600'
        }`}>
          {subcategory.name}
        </p>
      </div>
    </div>
  );
};

export default CategorySlugPage;
