"use client";
import Image from "next/image";
import Link from "next/link";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { LogOut, User, Settings } from "lucide-react";
import { useSession, signOut } from "next-auth/react";
import { useEffect, useState } from "react";

const UserActions = () => {
  const { data: session } = useSession();
  const [firstName, setFirstName] = useState("Guest");
  const [profilePic, setProfilePic] = useState(null);
  const [isUpdating, setIsUpdating] = useState(false);

  useEffect(() => {
    if (session?.user?.name) {
      setFirstName(session.user.name.split(" ")[0]);
    }
    // Dummy profile pic - you can replace this with actual user data
    setProfilePic(session?.user?.image || null);
  }, [session]);

  // Dummy update profile function
  const handleUpdateProfile = async () => {
    setIsUpdating(true);
    
    // Simulate API call delay
    await new Promise(resolve => setTimeout(resolve, 1000));
    
    // Dummy profile update logic
    const dummyUserData = {
      name: "John Doe",
      email: "john.doe@example.com",
      phone: "+1234567890",
      address: "123 Main St, City, State 12345"
    };
    
    // In a real app, you would make an API call here
    console.log("Updating profile with:", dummyUserData);
    
    // Simulate success
    alert("Profile updated successfully! (This is a dummy function)");
    
    setIsUpdating(false);
  };

  const handleLogout = async () => {
    try {
      await signOut({
        redirect: false,
        callbackUrl: '/'
      });
      
      // Clear any local storage if needed
      if (typeof window !== 'undefined') {
        localStorage.removeItem('user_preferences');
      }
      
      window.location.href = '/';
    } catch (error) {
      console.error('Logout failed:', error);
      // Force redirect even if signOut fails
      window.location.href = '/';
    }
  };

  const getUserInitial = () => {
    const name = session?.user?.name || firstName;
    return name ? name.charAt(0).toUpperCase() : "U";
  };

  // Dummy data for cart and notifications
  const cartItems = 3;
  const notifications = 5;

  return (
    <div className="flex items-center gap-2 sm:gap-3">
      {/* Cart */}
      <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-transform">
        <span className="sr-only">Cart</span>
        <svg
          className="w-6 h-6 text-gray-700"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z"
          />
        </svg>
        {cartItems > 0 && (
          <span className="absolute -top-1 -right-1 bg-red-500 text-white text-xs font-bold rounded-full w-5 h-5 flex items-center justify-center shadow-sm">
            {cartItems}
          </span>
        )}
      </button>

      {/* Notifications */}
      <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-transform">
        <span className="sr-only">Notifications</span>
        <svg
          className="w-6 h-6 text-gray-700"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M15 17h5l-1.405-1.405A2.032 2.032 0 0118 14.158V11a6.002 6.002 0 00-4-5.659V5a2 2 0 10-4 0v.341C7.67 6.165 6 8.388 6 11v3.159c0 .538-.214 1.055-.595 1.436L4 17h5m6 0v1a3 3 0 11-6 0v-1m6 0H9"
          />
        </svg>
        {notifications > 0 && (
          <span className="absolute -top-1 -right-1 bg-red-500 text-white text-xs font-bold rounded-full w-5 h-5 flex items-center justify-center shadow-sm">
            {notifications}
          </span>
        )}
      </button>

      {/* Profile Dropdown */}
      <DropdownMenu>
        <DropdownMenuTrigger className="flex items-center gap-2 p-3 rounded-lg transition hover:bg-gray-50 group">
          <div className="relative">
            {profilePic ? (
              <Image
                src={profilePic}
                alt="Profile"
                width={40}
                height={40}
                className="rounded-full border-2 border-yellow-400"
                onError={(e) => {
                  e.currentTarget.style.display = 'none';
                  e.currentTarget.nextSibling.style.display = 'flex';
                }}
              />
            ) : null}
            <div className={`rounded-full border-2 border-yellow-400 w-10 h-10 flex items-center justify-center bg-amber-500 text-white text-lg font-bold ${profilePic ? 'hidden' : ''}`}>
              {getUserInitial()}
            </div>
            <div className="absolute bottom-0 right-0 w-3 h-3 bg-green-500 rounded-full border-2 border-white"></div>
          </div>
          <span className="text-gray-700 font-medium hidden lg:inline-block text-sm">
            {firstName}
          </span>
        </DropdownMenuTrigger>
        <DropdownMenuContent
          align="end"
          className="w-48 bg-white shadow-xl rounded-xl p-2 border border-gray-100"
        >
          <DropdownMenuItem asChild>
            <Link
              href="/profile"
              className="flex items-center gap-2 px-3 py-2.5 text-gray-700 hover:bg-yellow-50 rounded-lg font-medium cursor-pointer transition"
            >
              <User size={16} />
              Profile
            </Link>
          </DropdownMenuItem>
          <DropdownMenuItem
            onClick={handleUpdateProfile}
            disabled={isUpdating}
            className="flex items-center gap-2 px-3 py-2.5 text-gray-700 hover:bg-yellow-50 rounded-lg font-medium cursor-pointer transition disabled:opacity-50 disabled:cursor-not-allowed"
          >
            <Settings size={16} />
            {isUpdating ? "Updating..." : "Update Profile"}
          </DropdownMenuItem>
          <DropdownMenuItem
            onClick={handleLogout}
            className="flex items-center gap-2 px-3 py-2.5 text-red-500 hover:bg-red-50 rounded-lg font-medium cursor-pointer transition"
          >
            <LogOut size={16} />
            Logout
          </DropdownMenuItem>
        </DropdownMenuContent>
      </DropdownMenu>
    </div>
  );
};

export default UserActions;
