"use client";

import Image from 'next/image';
import { Heart, Star, ShoppingCart, Image as ImageIcon, Clock, MapPin, Calendar, Tag } from 'lucide-react';
import { cn, formatPrice, truncateText } from '@/lib/utils';
import { motion } from 'framer-motion';
import { Button } from "@/app/components/ui/button";
import { useState, useEffect } from "react";
import { useCart } from '@/hooks/useCart';

// Timer hook
const useTimer = (endTime) => {
  const [timeLeft, setTimeLeft] = useState({
    hours: 0,
    minutes: 0,
    seconds: 0,
    expired: false
  });

  useEffect(() => {
    if (!endTime) return;

    const timer = setInterval(() => {
      const now = Date.now();
      const difference = endTime - now;

      if (difference <= 0) {
        setTimeLeft({
          hours: 0,
          minutes: 0,
          seconds: 0,
          expired: true
        });
        clearInterval(timer);
      } else {
        const hours = Math.floor(difference / (1000 * 60 * 60));
        const minutes = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((difference % (1000 * 60)) / 1000);

        setTimeLeft({
          hours,
          minutes,
          seconds,
          expired: false
        });
      }
    }, 1000);

    return () => clearInterval(timer);
  }, [endTime]);

  return timeLeft;
};

const ProductCard = ({ product, className, onAddToCart, onAddToWishlist }) => {
  // Handle both old and new product data structures
  const getProductData = () => {
    if (!product) return {};
    
    // New API structure
    if (product._id) {
      return {
        id: product._id,
        name: product.name || "Unnamed Product",
        brand: product.brand || "Generic",
        seller: product.vendor_store_id?.store_name || "Unknown Seller",
        image: getPrimaryImage(product.images),
        weight: "", // Not in new structure, could be added to attributes
        originalPrice: getPriceFromNewStructure(product),
        discountedPrice: getDiscountedPriceFromNewStructure(product),
        discount: calculateDiscountFromNewStructure(product),
        time: getTimingInfo(product.timing),
        rating: product.rating,
        status: product.status,
        is_featured: product.is_featured,
        is_best_seller: product.is_best_seller,
        is_new_arrival: product.is_new_arrival,
        category: product.category?.name,
        subcategory: product.subcategory?.name,
        tags: product.tags || [],
        warranty: product.warranty,
        description: product.description,
        product_type: product.product_type,
        attributes: product.attributes || [],
        specifications: product.specifications || [],
        timing: product.timing,
        type_specific: product.type_specific,
        return_policy: product.return_policy,
        seo: product.seo,
        availability: product.availability
      };
    }
    
    // Old structure (fallback)
    return {
      id: product.id || "",
      name: product.name || "Unnamed Product",
      brand: product.brand || "Generic",
      seller: product.seller || "Unknown Seller",
      image: product.image || "/home/grocery/broccoli.png",
      weight: product.weight || "",
      originalPrice: product.originalPrice || 0,
      discountedPrice: product.discountedPrice || 0,
      discount: product.discount || 0,
      time: product.time || 0,
      rating: product.rating,
      status: product.status,
      is_featured: product.is_featured,
      is_best_seller: product.is_best_seller,
      is_new_arrival: product.is_new_arrival,
      category: product.category,
      subcategory: product.subcategory,
      tags: product.tags || [],
      warranty: product.warranty,
      description: product.description,
      product_type: product.product_type,
      attributes: product.attributes || [],
      specifications: product.specifications || [],
      timing: product.timing,
      type_specific: product.type_specific,
      return_policy: product.return_policy,
      seo: product.seo,
      availability: product.availability
    };
  };

  // Get primary image from images array
  const getPrimaryImage = (images) => {
    if (!images || !Array.isArray(images) || images.length === 0) {
      return "/home/grocery/broccoli.png";
    }
    
    // Find primary image
    const primaryImage = images.find(img => img.is_primary);
    if (primaryImage && primaryImage.url) {
      return primaryImage.url;
    }
    
    // Fallback to first image
    return images[0]?.url || "/home/grocery/broccoli.png";
  };

  // Get price from variants
  const getPriceFromVariants = (variants, priceType) => {
    try {
      if (!variants || !Array.isArray(variants) || variants.length === 0) {
        return 0;
      }
      
      // Get the first variant with the specified price type
      const variant = variants.find(v => v.price && v.price[priceType]);
      return variant ? Number(variant.price[priceType] || 0) : 0;
    } catch (error) {
      console.error('Error getting price from variants:', error);
      return 0;
    }
  };

  // Calculate discount percentage
  const calculateDiscount = (variants) => {
    try {
      if (!variants || !Array.isArray(variants) || variants.length === 0) {
        return 0;
      }
      
      const variant = variants[0];
      if (variant.price && variant.price.base_price && variant.price.sale_price) {
        const basePrice = Number(variant.price.base_price);
        const salePrice = Number(variant.price.sale_price);
        if (basePrice > 0) {
          const discount = ((basePrice - salePrice) / basePrice) * 100;
          return Math.round(discount);
        }
      }
      
      return Number(variant.price?.discount_percentage || 0);
    } catch (error) {
      console.error('Error calculating discount from variants:', error);
      return 0;
    }
  };

  // Get timing information
  const getTimingInfo = (timing) => {
    if (!timing) return null;
    
    return {
      start_date: timing.start_date,
      end_date: timing.end_date,
      daily_start_time: timing.daily_start_time,
      daily_end_time: timing.daily_end_time,
      is_recurring: timing.is_recurring,
      recurring_pattern: timing.recurring_pattern
    };
  };

  // Get type-specific display info
  const getTypeSpecificInfo = (product_type, type_specific) => {
    if (!type_specific) return null;
    
    switch (product_type) {
      case 'movie_ticket':
        return {
          duration: type_specific.movie?.duration,
          language: type_specific.movie?.language,
          genre: type_specific.movie?.genre,
          age_rating: type_specific.movie?.age_rating,
          release_date: type_specific.movie?.release_date
        };
      case 'hotel_booking':
        return {
          check_in_time: type_specific.hotel?.check_in_time,
          check_out_time: type_specific.hotel?.check_out_time,
          amenities: type_specific.hotel?.amenities,
          room_types: type_specific.hotel?.room_types,
          star_rating: type_specific.hotel?.star_rating,
          location: type_specific.hotel?.location
        };
      case 'buffet_ticket':
        return {
          cuisine_type: type_specific.buffet?.cuisine_type,
          meal_type: type_specific.buffet?.meal_type,
          duration: type_specific.buffet?.duration,
          seating_capacity: type_specific.buffet?.seating_capacity
        };
      case 'event_ticket':
        return {
          event_type: type_specific.event?.event_type,
          venue: type_specific.event?.venue,
          start_date: type_specific.event?.start_date,
          end_date: type_specific.event?.end_date,
          age_restriction: type_specific.event?.age_restriction
        };
      case 'service':
        return {
          duration: type_specific.service?.duration,
          service_type: type_specific.service?.service_type,
          provider_qualification: type_specific.service?.provider_qualification,
          service_area: type_specific.service?.service_area
        };
      default:
        return null;
    }
  };

  // Get price from new variant_price structure
  const getPriceFromNewStructure = (product) => {
    try {
      // First try the new variant_price structure
      if (product.variant_price) {
        return Number(product.variant_price.base_price || 0);
      }
      
      // Fallback to old variants structure
      return getPriceFromVariants(product.variants, 'base_price');
    } catch (error) {
      console.error('Error getting price from new structure:', error);
      return 0;
    }
  };

  // Get discounted price from new variant_price structure
  const getDiscountedPriceFromNewStructure = (product) => {
    try {
      // First try the new variant_price structure
      if (product.variant_price) {
        if (product.variant_price.sale_price && Number(product.variant_price.sale_price) > 0) {
          return Number(product.variant_price.sale_price);
        }
        return Number(product.variant_price.current_price || product.variant_price.base_price || 0);
      }
      
      // Fallback to old variants structure
      return getPriceFromVariants(product.variants, 'sale_price');
    } catch (error) {
      console.error('Error getting discounted price from new structure:', error);
      return 0;
    }
  };

  // Calculate discount from new variant_price structure
  const calculateDiscountFromNewStructure = (product) => {
    try {
      // First try the new variant_price structure
      if (product.variant_price) {
        if (product.variant_price.discount_percentage && Number(product.variant_price.discount_percentage) > 0) {
          return Number(product.variant_price.discount_percentage);
        }
        if (product.variant_price.sale_price && product.variant_price.base_price) {
          const basePrice = Number(product.variant_price.base_price);
          const salePrice = Number(product.variant_price.sale_price);
          if (basePrice > 0) {
            const discount = ((basePrice - salePrice) / basePrice) * 100;
            return Math.round(discount);
          }
        }
        return 0;
      }
      
      // Fallback to old variants structure
      return calculateDiscount(product.variants);
    } catch (error) {
      console.error('Error calculating discount from new structure:', error);
      return 0;
    }
  };

  // Helper function to safely handle rating data
  const getSafeRating = (rating) => {
    if (!rating) return { average: 0, count: 0 };
    
    return {
      average: Number(rating.average || 0),
      count: Number(rating.count || 0)
    };
  };

  const productData = getProductData();
  const {
    id = "",
    name = "Unnamed Product",
    brand = "Generic",
    seller = "Unknown Seller",
    image = "/home/grocery/broccoli.png",
    weight = "",
    originalPrice = 0,
    discountedPrice = 0,
    discount = 0,
    time = 0,
    rating = { average: 0, count: 0 },
    status = "active",
    is_featured = false,
    is_best_seller = false,
    is_new_arrival = false,
    category = "",
    subcategory = "",
    tags = [],
    warranty = "",
    description = "",
    product_type = "retail",
    attributes = [],
    specifications = [],
    timing = null,
    type_specific = null,
    return_policy = null,
    seo = null,
    availability = []
  } = productData;

  // Get safe rating data
  const safeRating = getSafeRating(rating);

  const timeLeft = useTimer(time || 0);
  const { addItem, isInCart } = useCart();
  const [imageError, setImageError] = useState(false);
  const [showDetails, setShowDetails] = useState(false);

  const handleAddToCart = () => {
    if (!isInCart(id) && id) {
      addItem({
        id,
        name,
        brand,
        seller,
        price: discountedPrice,
        originalPrice,
        image,
        weight,
      });
    }
  };

  const handleImageError = () => {
    setImageError(true);
  };

  const displayImage = imageError ? "/home/grocery/broccoli.png" : image;
  const typeSpecificInfo = getTypeSpecificInfo(product_type, type_specific);

  // Format product type for display
  const formatProductType = (type) => {
    return type.split('_').map(word => 
      word.charAt(0).toUpperCase() + word.slice(1)
    ).join(' ');
  };

  return (
    <div className="pb-2 group">
      <div
        className="w-full p-3 border rounded-xl hover:shadow-lg transition-all duration-300 bg-white shadow-sm"
        role="group"
        aria-label={`Product card for ${name}`}
      >
        {/* Product Type Badge */}
        <div className="absolute top-2 left-2 z-10">
          <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-gray-100 text-gray-800">
            {formatProductType(product_type)}
          </span>
        </div>

        {/* Product Status Badges */}
        <div className="absolute top-2 right-2 z-10 flex flex-wrap gap-1">
          {is_featured && (
            <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-purple-100 text-purple-800">
              Featured
            </span>
          )}
          {is_best_seller && (
            <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-blue-100 text-blue-800">
              Best Seller
            </span>
          )}
          {is_new_arrival && (
            <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-green-100 text-green-800">
              New
            </span>
          )}
          {/* Show variants indicator */}
          {(product.variants && product.variants.length > 0) && (
            <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-orange-100 text-orange-800">
              {product.variants.length} Variant{product.variants.length > 1 ? 's' : ''}
            </span>
          )}
        </div>

        <div className="relative flex items-center justify-center w-full h-[142px] bg-blue-50 rounded-xl p-6">
          <Image
            src={displayImage}
            alt={name || "Product image"}
            width={158}
            height={100}
            className="w-[148px] h-[90px] object-contain"
            onError={handleImageError}
          />

          <Button
            onClick={handleAddToCart}
            disabled={!id}
            className={`absolute bottom-2 right-10 transform translate-y-1/2 translate-x-1/2 w-[53px] h-[33px] border font-medium rounded-md hover:bg-blue-100 transition shadow-md
              ${isInCart(id) ? "bg-green-50 text-green-500 border-green-400" : "bg-white text-blue-400 border-blue-400"}`}
            aria-label={isInCart(id) ? "Item added to cart" : "Add item to cart"}
          >
            {isInCart(id) ? "" : "ADD"}
          </Button>
        </div>

        <h3 className="font-bold text-sm mt-2 line-clamp-1">{name}</h3>
        <p className="text-xs text-gray-500 line-clamp-1">({brand})</p>
        <p className="text-xs text-gray-500">{weight || "1 unit"}</p>
        <p className="text-xs text-gray-500 line-clamp-1">By {seller}</p>

        {/* Category and Subcategory */}
        <div className="flex items-center gap-2 mt-1">
          {category && (
            <span className="text-xs bg-blue-50 text-blue-600 px-1 py-0.5 rounded">
              {category}
            </span>
          )}
          {subcategory && (
            <span className="text-xs bg-green-50 text-green-600 px-1 py-0.5 rounded">
              {subcategory}
            </span>
          )}
        </div>

        {/* Rating */}
        {safeRating.average > 0 && (
          <div className="flex items-center gap-1 mt-1">
            <Star className="w-3 h-3 fill-yellow-400 text-yellow-400" />
            <span className="text-xs text-gray-600">
              {Number(safeRating.average).toFixed(1)} ({safeRating.count})
            </span>
          </div>
        )}

        {/* Type-specific Information */}
        {typeSpecificInfo && (
          <div className="mt-2 space-y-1">
            {product_type === 'movie_ticket' && (
              <>
                {typeSpecificInfo.duration && (
                  <div className="flex items-center gap-1 text-xs text-gray-600">
                    <Clock className="w-3 h-3" />
                    <span>{typeSpecificInfo.duration} min</span>
                  </div>
                )}
                {typeSpecificInfo.language && (
                  <div className="text-xs text-gray-600">
                    Language: {typeSpecificInfo.language}
                  </div>
                )}
                {typeSpecificInfo.genre && typeSpecificInfo.genre.length > 0 && (
                  <div className="text-xs text-gray-600">
                    Genre: {typeSpecificInfo.genre.join(', ')}
                  </div>
                )}
              </>
            )}
            
            {product_type === 'hotel_booking' && (
              <>
                {typeSpecificInfo.star_rating && (
                  <div className="flex items-center gap-1 text-xs text-gray-600">
                    <Star className="w-3 h-3 fill-yellow-400 text-yellow-400" />
                    <span>{typeSpecificInfo.star_rating} Star Hotel</span>
                  </div>
                )}
                {typeSpecificInfo.location && (
                  <div className="flex items-center gap-1 text-xs text-gray-600">
                    <MapPin className="w-3 h-3" />
                    <span>{typeSpecificInfo.location.city}, {typeSpecificInfo.location.state}</span>
                  </div>
                )}
              </>
            )}
            
            {product_type === 'buffet_ticket' && (
              <>
                {typeSpecificInfo.meal_type && (
                  <div className="text-xs text-gray-600 capitalize">
                    Meal: {typeSpecificInfo.meal_type}
                  </div>
                )}
                {typeSpecificInfo.cuisine_type && typeSpecificInfo.cuisine_type.length > 0 && (
                  <div className="text-xs text-gray-600">
                    Cuisine: {typeSpecificInfo.cuisine_type.join(', ')}
                  </div>
                )}
              </>
            )}
            
            {product_type === 'event_ticket' && (
              <>
                {typeSpecificInfo.event_type && (
                  <div className="text-xs text-gray-600 capitalize">
                    Event: {typeSpecificInfo.event_type}
                  </div>
                )}
                {typeSpecificInfo.venue && (
                  <div className="flex items-center gap-1 text-xs text-gray-600">
                    <MapPin className="w-3 h-3" />
                    <span>{typeSpecificInfo.venue.name}</span>
                  </div>
                )}
              </>
            )}
            
            {product_type === 'service' && (
              <>
                {typeSpecificInfo.duration && (
                  <div className="flex items-center gap-1 text-xs text-gray-600">
                    <Clock className="w-3 h-3" />
                    <span>{typeSpecificInfo.duration} min</span>
                  </div>
                )}
                {typeSpecificInfo.service_type && (
                  <div className="text-xs text-gray-600">
                    Service: {typeSpecificInfo.service_type}
                  </div>
                )}
              </>
            )}
          </div>
        )}

        {/* Timing Information */}
        {timing && (
          <div className="mt-2 space-y-1">
            {timing.start_date && timing.end_date && (
              <div className="flex items-center gap-1 text-xs text-gray-600">
                <Calendar className="w-3 h-3" />
                <span>
                  {new Date(timing.start_date).toLocaleDateString()} - {new Date(timing.end_date).toLocaleDateString()}
                </span>
              </div>
            )}
            {timing.daily_start_time && timing.daily_end_time && (
              <div className="flex items-center gap-1 text-xs text-gray-600">
                <Clock className="w-3 h-3" />
                <span>{timing.daily_start_time} - {timing.daily_end_time}</span>
              </div>
            )}
            {timing.is_recurring && (
              <div className="text-xs text-gray-600 capitalize">
                Recurring: {timing.recurring_pattern}
              </div>
            )}
          </div>
        )}

        {/* Tags */}
        {tags && tags.length > 0 && (
          <div className="flex flex-wrap gap-1 mt-2">
            {tags.slice(0, 3).map((tag, index) => (
              <span key={index} className="text-xs bg-gray-100 text-gray-600 px-1 py-0.5 rounded flex items-center gap-1">
                <Tag className="w-2 h-2" />
                {tag}
              </span>
            ))}
            {tags.length > 3 && (
              <span className="text-xs text-gray-400">+{tags.length - 3} more</span>
            )}
          </div>
        )}

        {/*  Deal Countdown - Only if time is present */}
        {time && (
          <div className="mt-2">
            {timeLeft.expired ? (
              <div className="text-center text-red-500 text-xs font-medium py-2">
                Offer Expired
              </div>
            ) : (
              <div className="flex flex-col items-center bg-gray-100 rounded p-1">
                <div className="flex items-center justify-center gap-1 text-blue-700 text-sm font-medium">
                  <span>{String(timeLeft.hours).padStart(2, "0")}</span>
                  <span>:</span>
                  <span>{String(timeLeft.minutes).padStart(2, "0")}</span>
                  <span>:</span>
                  <span>{String(timeLeft.seconds).padStart(2, "0")}</span>
                </div>
                <div className="flex items-center justify-center gap-4 text-gray-500 text-[10px] mt-1">
                  <span className="w-6 text-center">hours</span>
                  <span className="w-6 text-center">min</span>
                  <span className="w-6 text-center">sec</span>
                </div>
              </div>
            )}
          </div>
        )}

        {/* Price Section */}
        <div className="mt-2">
          {discount > 0 && (
            <p className="text-sm text-blue-700 font-semibold">
              {discount}% OFF
            </p>
          )}
          <div className="flex items-center gap-2">
            {originalPrice > discountedPrice && (
              <p className="text-xs text-gray-400 line-through">
                {originalPrice}  
              </p>
            )}
            <p className="text-sm font-bold text-green-600">
              {discountedPrice || originalPrice}
            </p>
          </div>
          
          {/* Show variant price details if available */}
          {product.variant_price && (
            <div className="mt-1 text-xs text-gray-500">
              <div className="flex justify-between">
                <span>Base Price:</span>
                <span>{product.variant_price.base_price || 0}</span>
              </div>
              {product.variant_price.sale_price && product.variant_price.sale_price > 0 && (
                <div className="flex justify-between text-green-600">
                  <span>Sale Price:</span>
                  <span>{product.variant_price.sale_price}</span>
                </div>
              )}
              {product.variant_price.discount_percentage && product.variant_price.discount_percentage > 0 && (
                <div className="flex justify-between text-blue-600">
                  <span>Discount:</span>
                  <span>{product.variant_price.discount_percentage}%</span>
                </div>
              )}
              <div className="flex justify-between font-medium">
                <span>Current Price:</span>
                <span>{product.variant_price.current_price || product.variant_price.base_price || 0}</span>
              </div>
            </div>
          )}
        </div>

        {/* Additional Details Toggle */}
        {(attributes.length > 0 || specifications.length > 0 || warranty || return_policy) && (
          <div className="mt-2">
            <button
              onClick={() => setShowDetails(!showDetails)}
              className="text-xs text-blue-600 hover:text-blue-800 underline"
            >
              {showDetails ? 'Hide Details' : 'Show Details'}
            </button>
            
            {showDetails && (
              <div className="mt-2 space-y-2 text-xs text-gray-600">
                {/* Attributes */}
                {attributes.length > 0 && (
                  <div>
                    <div className="font-medium mb-1">Attributes:</div>
                    <div className="space-y-1">
                      {attributes.map((attr, index) => (
                        <div key={index} className="flex justify-between">
                          <span>{attr.name}:</span>
                          <span>{attr.value}</span>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Specifications */}
                {specifications.length > 0 && (
                  <div>
                    <div className="font-medium mb-1">Specifications:</div>
                    <div className="space-y-1">
                      {specifications.map((spec, index) => (
                        <div key={index} className="flex justify-between">
                          <span>{spec.name}:</span>
                          <span>{spec.value}</span>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Warranty */}
                {warranty && (
                  <div>
                    <div className="font-medium">Warranty:</div>
                    <div>{warranty}</div>
                  </div>
                )}

                {/* Return Policy */}
                {return_policy && return_policy.allowed && (
                  <div>
                    <div className="font-medium">Return Policy:</div>
                    <div>{return_policy.days} days return policy</div>
                    {return_policy.conditions.length > 0 && (
                      <div className="mt-1">
                        <div className="font-medium">Conditions:</div>
                        <ul className="list-disc list-inside space-y-1">
                          {return_policy.conditions.map((condition, index) => (
                            <li key={index}>{condition}</li>
                          ))}
                        </ul>
                      </div>
                    )}
                  </div>
                )}
              </div>
            )}
          </div>
        )}

        {/* Warranty (always visible) */}
        {warranty && !showDetails && (
          <p className="text-xs text-gray-500 mt-1">{warranty}</p>
        )}
      </div>
    </div>
  );
};

export default ProductCard; 