"use client";

import { useState, useEffect } from "react";
import axios from "axios";
import CategoryCarousel from "@/app/components/home/CategoryCarousel";
import Footer from "@/app/components/layout/Footer";
import Header from "@/app/components/layout/Header";  
import { generateDummyProducts } from "@/lib/utils";
import Image from "next/image";
import Link from "next/link";

const CategoryPage = () => {
  const [categories, setCategories] = useState([]);
  const [featuredProducts, setFeaturedProducts] = useState([]);
  const [bestSellerProducts, setBestSellerProducts] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);



  useEffect(() => {
    const fetchCategoriesData = async () => {
      try {
        setIsLoading(true);
        setError(null);
        
        // Fetch categories from API
        const response = await axios.get('http://localhost:4000/lmd/api/v1/retail/categories', {
          headers: {
            'Content-Type': 'application/json',
      
          }
        });

        if (response.data.success) {
          setCategories(response.data.data || []);
        } else {
          throw new Error('Failed to fetch categories');
        }
      } catch (err) {
        console.log('Using dummy data due to API error:', err.message);
        setError('Failed to load categories');
        // Use dummy data as fallback with proper image paths
        const dummyCategories = [
          { _id: 'cat-1', name: 'Electronics', slug: 'electronics', imageUrl: '/categories/all.png' },
          { _id: 'cat-2', name: 'Fashion', slug: 'fashion', imageUrl: '/categories/bath-beauty.png' },
          { _id: 'cat-3', name: 'Grocery', slug: 'grocery', imageUrl: '/categories/fruits-vegetables.png' },
          { _id: 'cat-4', name: 'Home & Living', slug: 'home-living', imageUrl: '/categories/home-care-hygiene.png' },
          { _id: 'cat-5', name: 'Sports', slug: 'sports', imageUrl: '/categories/baby-care.png' },
          { _id: 'cat-6', name: 'Beverages', slug: 'beverages', imageUrl: '/categories/beverages.png' },
          { _id: 'cat-7', name: 'Bakery & Dairy', slug: 'bakery-dairy', imageUrl: '/categories/bakery-dairy.png' },
          { _id: 'cat-8', name: 'Snacks', slug: 'snacks', imageUrl: '/categories/snacks.png' },
          { _id: 'cat-9', name: 'Kitchenware', slug: 'kitchenware', imageUrl: '/categories/kitchenware-appliances.png' },
          { _id: 'cat-10', name: 'Cleaning', slug: 'cleaning', imageUrl: '/categories/cleaning-household.png' },
          { _id: 'cat-11', name: 'Beauty', slug: 'beauty', imageUrl: '/categories/makeup.png' },
          { _id: 'cat-12', name: 'Desserts', slug: 'desserts', imageUrl: '/categories/desserts.png' },
        ];
        setCategories(dummyCategories);
      } finally {
        setIsLoading(false);
      }
    };

    const fetchFeaturedProducts = async () => {
      try {
        const response = await axios.get('http://localhost:4000/lmd/api/v1/retail/products/featured', {
          headers: {
            'Content-Type': 'application/json',
  
          },
          params: {
            limit: 10
          }
        });

        if (response.data.status === 'success') {
          setFeaturedProducts(response.data.data || []);
        } else {
          throw new Error('Failed to fetch featured products');
        }
      } catch (err) {
        console.log('Using dummy featured products due to API error:', err.message);
        setFeaturedProducts(generateDummyProducts(10));
      }
    };

    const fetchBestSellerProducts = async () => {
      try {
        const response = await axios.get('http://localhost:4000/lmd/api/v1/retail/products/best-sellers', {
          headers: {
            'Content-Type': 'application/json',

          },
          params: {
            limit: 10
          }
        });

        if (response.data.status === 'success') {
          setBestSellerProducts(response.data.data || []);
        } else {
          throw new Error('Failed to fetch best seller products');
        }
      } catch (err) {
        console.log('Using dummy best seller products due to API error:', err.message);
        setBestSellerProducts(generateDummyProducts(10));
      }
    };

    fetchCategoriesData();
    fetchFeaturedProducts();
    fetchBestSellerProducts();
  }, []);

  return (
    <main className="space-y-4 md:space-y-6">
      <Header />

      {/* Breadcrumb + Filters */}
      <section className="w-full max-w-[1700px] mx-auto px-4 md:px-6 mt-2 flex flex-col md:flex-row justify-between items-center gap-3">
        <nav className="text-lg font-semibold text-black">
          <Link href="/categories" className="hover:underline">
            Categories
          </Link>
        </nav>
        <div className="flex gap-2 w-full md:w-auto justify-center md:justify-end">
          <FilterButton>Filters</FilterButton>
          <FilterButton>Sort</FilterButton>
        </div>
      </section>

      {/* Categories grid */}
      <section className="w-full max-w-[1700px] mx-auto px-4 md:px-6">
        {isLoading ? (
          <div className="grid grid-cols-3 xs:grid-cols-4 sm:grid-cols-6 md:grid-cols-8 lg:grid-cols-10 xl:grid-cols-12 gap-3">
            {[...Array(12)].map((_, i) => (
              <div key={i} className="animate-pulse">
                <div className="flex flex-col items-center p-1">
                  <div className="w-12 h-12 md:w-14 md:h-14 bg-gray-200 rounded-lg mb-1"></div>
                  <div className="h-3 bg-gray-200 rounded w-12"></div>
                </div>
              </div>
            ))}
          </div>
        ) : error ? (
          <div className="text-center py-6 text-red-500">
            {error}
          </div>
        ) : (
          <div className="grid grid-cols-3 xs:grid-cols-4 sm:grid-cols-6 md:grid-cols-8 lg:grid-cols-10 xl:grid-cols-12 gap-3">
            {categories.map((category) => (
              <CategoryItem key={category._id} category={category} />
            ))}
          </div>
        )}
      </section>

      {/* Promo Banner - Commented out */}
      {/* <section className="w-full max-w-[1700px] mx-auto px-4 md:px-6">
        <ResponsivePromoBanner />
      </section> */}

      {/* Featured Products Row */}
      <section className="w-full max-w-[1700px] mx-auto px-4 md:px-6">
        <div className="mb-4">
          <h2 className="text-xl font-semibold text-gray-900 mb-2">Featured Products</h2>
        </div>
        <CategoryCarousel data={featuredProducts} loading={isLoading} />
      </section>

      {/* Best Sellers Row */}
      <section className="w-full max-w-[1700px] mx-auto px-4 md:px-6">
        <div className="mb-4">
          <h2 className="text-xl font-semibold text-gray-900 mb-2">Best Sellers</h2>
        </div>
        <CategoryCarousel data={bestSellerProducts} loading={isLoading} />
      </section>

      <CategoryFooter />
      <Footer />
    </main>
  );
};

export const FilterButton = ({ children }) => (
  <button className="flex items-center gap-1 px-2 py-1.5 border rounded-md hover:bg-gray-100 w-full md:w-auto justify-center text-sm">
    <Image
      src="/categories/icon.svg"
      alt="icon"
      width={16}
      height={16}
      className="shrink-0"
    />
    {children}
    <Image
      src="/categories/down.svg"
      alt="arrow"
      width={12}
      height={12}
      className="ml-1"
    />
  </button>
);

const CategoryItem = ({ category }) => (
  <Link href={`/categories/${category.slug}`} className="group">
    <div className="flex flex-col items-center p-1 hover:bg-gray-50 rounded-lg transition-all">
      <div className="relative w-12 h-12 md:w-14 md:h-14">
        <Image
          src={category.imageUrl || "/categories/default.png"}
          alt={category.name}
          fill
          className="object-contain"
          onError={(e) => {
            // Fallback to default image if the category image fails to load
            e.target.src = "/categories/default.png";
          }}
        />
      </div>
      <p className="text-center mt-1 text-xs md:text-sm font-medium group-hover:text-blue-600 truncate w-full">
        {category.name}
      </p>
    </div>
  </Link>
);

const ResponsivePromoBanner = () => (
  <div className="relative aspect-[4/1] w-full rounded-lg overflow-hidden">
    <Image
      src="/categories/category-bg.png"
      alt="Category Background"
      fill
      className="object-cover"
    />
  </div>
);

const CategoryFooter = () => (
  <div className="w-full max-w-[1700px] mx-auto px-4 md:px-6">
    {/* Add any footer content for categories page */}
  </div>
);

export default CategoryPage;
